<template>
  <nav class="nav">
    <div class="logo">
      <img src="@/assets/pachaqtec-logo.png" alt="Pacha-Q-Tec Logo">
    </div>
    <ul class="nav__list">
      <li
        class="nav__item"
        :class="{ 'nav__item--active' : activeTab == 'init' }"
        @click="() => this.changeTab('init')"
      >
        <router-link to="/" class="nav__link">
          <img src="../assets/home-gray.svg" alt="Home Icon" class="icon">
          <span>Inicio</span>
        </router-link>
      </li>
      <li
        class="nav__item"
        :class="{ 'nav__item--active' : activeTab == 'chat' }"
        @click="() => this.changeTab('chat')"
      >
        <router-link to="/chat" class="nav__link">
          <img src="../assets/chat-gray.svg" alt="Chat Icon" class="icon">
          <span>Mensajes</span>
        </router-link>
      </li>
      <li
        class="nav__item"
        :class="{ 'nav__item--active' : activeTab == 'account' }"
        @click="() => this.changeTab('account')"
      >
        <router-link to="/account" class="nav__link">
          <img src="../assets/user-gray.svg" alt="User Icon" class="icon">
          <span>Mi cuenta</span>
        </router-link>
      </li>
      <li class="nav__item nav__item--hidden" @click="logout">
        <router-link to="/" class="nav__link">
          <img src="../assets/logout-gray.svg" alt="Logout Icon" class="icon">
          <span>Cerrar sesión</span>
        </router-link>
      </li>
    </ul>
    <ul class="nav__list">
      <li class="nav__item" @click="logout">
        <router-link to="" class="nav__link">
          <img src="../assets/logout-gray.svg" alt="Logout Icon" class="icon">
          <span>Cerrar sesión</span>
        </router-link>
      </li>
    </ul>
  </nav>
</template>

<script>
import { mapMutations, mapState } from 'vuex';

export default {
  name: 'Nav',
  computed: {
    ...mapState({
      activeTab: (state) => state.accountStore.activeTab,
    }),
  },
  methods: {
    ...mapMutations({
      changeTab: 'accountStore/[ACCOUNT] SET_TAB',
      isLogged: 'authStore/[AUTH] SET_LOGIN',
      user: 'authStore/[AUTH] SET_USER',
    }),

    logout() {
      this.isLogged(false);
      this.user({});
      this.$router.replace('/login');
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../assets/styles/_Variables';

.nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-items: center;
  background-color: $color-primary;

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    .icon { margin-right: .75em; }

    & + & { margin-top: auto; }
  }

  &__item {
    color: $color-gray;
    font-weight: $bold;

    &--hidden { display: none; }

    &--active {
      background-color: $color-secondary;
      // color: white;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    padding: 1em 1.5em;
  }

  .logo {
    padding: 2em;
    text-align: center;

    img {
      max-width: 60px;
      height: auto;
      margin: 0 auto;
    }
  }

  @media (max-width: 900px) {
    height: auto;
    flex-direction: row;

    .logo { display: none; }

    &__list {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &__list + &__list { display: none; }

    &__item {
      &--active { background-color: inherit; }
      &--hidden { display: flex; }
    }

    &__link { padding: 1em; }
  }

  @media (max-width: 600px) {
    span { display: none; }

    &__item--active span { display: inline-block; }
  }
}
</style>
