function ordenarMayorMenor(users) {
  const usersList = users;
  for (let k = 1; k < usersList.length; k += 1) {
    for (let i = 0; i < (usersList.length - k); i += 1) {
      if (usersList[i].name > usersList[i + 1].name) {
        const aux = usersList[i];
        usersList[i] = usersList[i + 1];
        usersList[i + 1] = aux;
      }
    }
  }
  return usersList;
}

function ordenarMenorMayor(users) {
  const usersList = users;
  for (let k = 1; k < usersList.length; k += 1) {
    for (let i = 0; i < (usersList.length - k); i += 1) {
      if (usersList[i].name < usersList[i + 1].name) {
        const aux = usersList[i];
        usersList[i] = usersList[i + 1];
        usersList[i + 1] = aux;
      }
    }
  }
  return usersList;
}

const GETTERS = {
  filterUsers: (state) => (filters) => {
    let usersListos = state.users;
    if (filters[0].length !== 0) {
      if (filters[0][0] === 'A - Z') usersListos = ordenarMayorMenor(usersListos);
      else usersListos = ordenarMenorMayor(usersListos);
    }
    if (filters[1].length !== 0) {
      usersListos = usersListos.filter((m) => filters[1].includes(m.espe));
    }
    if (filters[2].length !== 0) {
      usersListos = usersListos.filter((m) => (m.name).includes(filters[2]));
    }
    return usersListos;
  },
};

export default GETTERS;
