import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '@/store/';
import Home from '@/views/Home.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: Home,
    children: [
      {
        path: 'firstLogin',
        component: () => import('@/views/account/FirstLogin.vue'),
      },
      {
        path: '/',
        component: () => import('@/views/account/Inicio.vue'),
        beforeEnter: (to, from, next) => {
          if (!store.state.authStore.user.firstTime) next();
          else next({ path: 'firstLogin' });
        },
      },
      {
        path: 'chat',
        component: () => import('@/views/account/Mensajes.vue'),
      },
      {
        path: 'account',
        component: () => import('@/views/account/Cuenta.vue'),
      },
    ],
    beforeEnter: (to, from, next) => {
      if (store.state.authStore.isLogged) next();
      else next({ name: 'Landing' });
    },
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('@/views/auth/Register.vue'),
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/auth/Login.vue'),
  },
  {
    path: '/landing',
    name: 'Landing',
    component: () => import('@/views/Landing.vue'),
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
