import { REGISTER_SUCCESS, SET_LOGIN, SET_USER } from './mutation-type';

const ACTIONS = {

  register({ commit }, payload) {
    fetch('https://my-json-server.typicode.com/xvitkleo/Pachaqtec/users', {
      method: 'POST',
      body: JSON.stringify(payload),
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
      },
    })
      .then((res) => res.json())
      .then(() => commit(REGISTER_SUCCESS, true))
      .catch(() => commit(REGISTER_SUCCESS, false));
  },

  login({ commit }, payload) {
    fetch('https://my-json-server.typicode.com/xvitkleo/Pachaqtec/users')
      .then((res) => res.json())
      .then((users) => {
        const foundUser = users
          .find((user) => user.email === payload.email && user.password === payload.password);
        if (foundUser) {
          commit(SET_USER, foundUser);
          commit(SET_LOGIN, true);
        } else {
          commit(SET_LOGIN, false);
        }
      });
  },

  updateProfile({ commit }, payload) {
    fetch(`https://my-json-server.typicode.com/xvitkleo/Pachaqtec/users/${payload.id}`, {
      method: 'PATCH',
      body: JSON.stringify({
        about: payload.about,
        age: payload.age,
        phone: payload.phone,
        espe: payload.espe,
        campus: payload.campus,
        firstTime: false,
        picture_Url: payload.picture_Url,
      }),
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
      },
    })
      .then((res) => res.json())
      .then((user) => commit(SET_USER, user));
  },

  changePassword({ commit }, payload) {
    fetch(`https://my-json-server.typicode.com/xvitkleo/Pachaqtec/users/${payload.id}`, {
      method: 'PATCH',
      body: JSON.stringify({
        password: payload.password,
      }),
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
      },
    })
      .then((res) => res.json())
      .then((user) => commit(SET_USER, user));
  },
};

export default ACTIONS;
