import { SET_USERS } from './mutation-type';

const ACTIONS = {
  loadUsers({ commit }) {
    fetch('https://my-json-server.typicode.com/xvitkleo/Pachaqtec/users')
      .then((response) => response.json())
      .then((users) => {
        commit(SET_USERS, users);
      });
  },
};
export default ACTIONS;
