import { REGISTER_SUCCESS, SET_LOGIN, SET_USER } from './mutation-type';

const MUTATIONS = {
  [REGISTER_SUCCESS](state, payload) {
    state.registerSuccessful = payload;
  },
  [SET_LOGIN](state, payload) {
    state.isLogged = payload;
  },
  [SET_USER](state, payload) {
    state.user = payload;
  },
};

export default MUTATIONS;
